<template>
  <div>
      <div v-for="(customer,key) in customerList">
          <div>{{customer.name}}</div>
          <div>{{customer.surname}}</div>
      </div>
  </div>
</template>
<script>
export default {
  name: "List",
  data() {
    return {
      customerList: [
        { name: "Erdo", surname: "Oksuz" },
        { name: "Fenasi", surname: "Akyüz" },
        { name: "Tuffy", surname: "Fak" },
        { name: "Baklava", surname: "yee" }
      ]
    };
  }
};
</script>
<style >

</style>



